"use client";

import React, { createContext, useContext, useEffect, useState, ReactNode } from "react";
import {
  getAuth,
  onAuthStateChanged,
  signInWithPopup,
  GoogleAuthProvider,
  GithubAuthProvider,
  signOut,
  User,
} from "firebase/auth";
import app from "./firebase";

type ProviderId = "google" | "github";

type AuthContextValue = {
  user: User | null;
  isLoading: boolean;
  error: string | null;
  login: (provider?: ProviderId) => Promise<void>;
  logout: () => Promise<void>;
};

const AuthContext = createContext<AuthContextValue | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
  const auth = getAuth(app);

  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const login = async (provider: ProviderId = "google") => {
    setError(null);
    try {
      const prov = provider === "github" ? new GithubAuthProvider() : new GoogleAuthProvider();
      await signInWithPopup(auth, prov);
    } catch (e: unknown) {
      setError(e instanceof Error ? e.message : "Login failed");
    }
  };

  const logout = async () => {
    setError(null);
    try {
      await signOut(auth);
    } catch (e: unknown) {
      setError(e instanceof Error ? e.message : "Logout failed");
    }
  };

  useEffect(() => {
    const unsub = onAuthStateChanged(auth, (u) => {
      setUser(u);
      setIsLoading(false);
      // jednoduch√° perzistencia
      if (u) sessionStorage.setItem("fb_user", u.uid);
      else sessionStorage.removeItem("fb_user");
    });
     
  }, [auth]);

  return (
    <AuthContext.Provider value={{ user, isLoading, error, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth(): AuthContextValue {
  const ctx = useContext(AuthContext);
  if (!ctx) throw new Error("useAuth must be used within AuthProvider");
  return ctx;
}
